.App {
  max-width:800px;
  margin: 6rem auto;
  padding: 0 1rem;
  /* text-align: center; */
}

.intro {
  /* text-align:center; */
  /* padding:4rem; */
  border-bottom: 1px solid #eee;
  background-color: #8BC6EC;
  background-image: -webkit-linear-gradient(315deg, #8BC6EC 0%, #9599E2 100%);
  background-image: -o-linear-gradient(315deg, #8BC6EC 0%, #9599E2 100%);
  background-image: linear-gradient(135deg, #8BC6EC 0%, #9599E2 100%);
  color:#fff;
  margin-top:4rem;
}
.intro a {
  color: #fff;
}
.intro h1 {
  font-size:4rem;
}

.intro-actions {
  margin:1rem 0;
}


